@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html,body{background-color:#FFE3F1;cursor:none;-webkit-touch-callout:none;-webkit-user-select:none}body::-webkit-scrollbar{display:none}section{display:block;background-image:url(data:image/png;base64,)}.cursor{width:20px;height:20px;background-color:cornsilk;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;z-index:9999;mix-blend-mode:difference;transition:all 500ms ease;transition-property:opacity}.cursor--hidden{opacity:0}

*{margin:0;padding:0;outline:0;font-size:100%;vertical-align:baseline;background:0 0}.nav{left:0;right:0;position:fixed;overflow:hidden}nav{display:block;padding:2rem 7.5rem 2rem 7.5rem}nav li{display:list-item;margin-left:3.12vw}.nav-wrapper{display:flex;justify-content:space-between}ul{display:block;-webkit-margin-before:1em;margin-block-start:1em;scroll-margin-block-end:1em;-webkit-margin-start:0px;margin-inline-start:0px;-webkit-margin-end:0px;margin-inline-end:0px;-webkit-padding-start:40px;padding-inline-start:40px}.nav-links{display:flex;flex-direction:row;list-style:none;text-decoration:none;z-index:999}.nav-link{position:relative}.nav-link::after{content:'';position:absolute;width:0;height:3px;display:block;margin-top:5px;right:0;background:#fff;transition:width .2s ease;-webkit-transition:width .2s ease}.nav-link:hover::after{width:100%;left:0;background:#fff}

#work{background-color:green;min-height:100vh}#work .flex{align-items:center;display:flex;flex-direction:column;justify-content:center}#work section{position:relative}

#aboutMe{background-color:yellow;min-height:100vh}#aboutMe .flex{align-items:center;display:flex;flex-direction:column;justify-content:center}#aboutMe section{position:relative}

#contact{background-color:blue;min-height:100vh}#contact .flex{align-items:center;display:flex;flex-direction:column;justify-content:center}#contact section{position:relative}

#homePage{background-color:red;min-height:100vh;padding-top:8rem}#homePage .flex{align-items:center;display:flex;flex-direction:column;justify-content:center}

